<template>
  <div id="home">
    <t-header :name="name" :product="product"></t-header>
    <div class="line"></div>
    <t-cont :lists="product"></t-cont>
  </div>
</template>
<script>
import THeader from './vheader'
import TCont from '../components/for/for'
import { getProduct } from '../api'

const cat_id = window.location.href.split('?')[1].split('=')[1]
const fromname = window.location.href.split('&')[1].split('=')[1]
const name = decodeURI(fromname)
export default {
  data () {
    return {
      product: [],
      name: ''
    }
  },
  mounted () {
    this.name = name
  },
  created () {
    getProduct({ cat_id }).then((data) => {
      console.log(cat_id)
      this.product = data
    })
  },
  components: {
    THeader,
    TCont
  }
}
</script>
<style>
  body {
    margin: 0;
    padding: 0;
    color: rgba(0, 0, 0, 0.81);
  }
  .line {
    width: 100%;
    border-bottom: 1px solid rgba(1, 12, 23, .4);
  }
</style>
